.header {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 80px;
  background-color: rgb(31, 31, 31);
  padding: 0px 40px 0px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 10;
}

.header h1 {
  color: white;
  font-size: 2rem;
  width: fit-content;
}

.account-icon {
  width: 35px;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
  border-radius: 50%;
  padding: 3px;
  cursor: pointer;
}

.account-icon img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.account-menu {
  position: absolute;
  right: 15px;
  top: 96px;
  background-color: rgb(31, 31, 31);
  width: fit-content;
  height: fit-content;
  padding: 20px;
  border-radius: 10px;
}

.auth-div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.account-menu::after {
  background-color: rgb(31, 31, 31);
  content: "";
  position: absolute;
  right: 28px;
  top: -10px;
  width: 30px;
  height: 30px;
  border-top-left-radius: 5px;
  transform: rotate(45deg);
  z-index: -1;
}

.auth-div h3 {
  color: #ffffff;
  font-weight: 500;
  font-size: 20px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell,
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
}

.auth-div button {
  cursor: pointer;
  background-color: white;
  transition: background-color 0.3s, box-shadow 0.3s;
  width: fit-content;

  border: none;
  border-radius: 3px;
  box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.04), 0 1px 1px rgba(0, 0, 0, 0.25);

  color: rgb(88, 88, 88);
  font-size: 14px;
  font-weight: 500;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell,
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
}

.auth-div button:active {
  background-color: #eeeeee;
}

.auth-div button:focus {
  outline: none;
  box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.04), 0 2px 4px rgba(0, 0, 0, 0.25), 0 0 0 3px #c8dafc;
}

.auth-div button:disabled {
  filter: grayscale(100%);
  background-color: #ebebeb;
  box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.04), 0 1px 1px rgba(0, 0, 0, 0.25);
  cursor: not-allowed;
}

.login-with-google-btn {
  padding: 12px 16px 12px 42px;
  background-image: url(data:image/svg+xml;base64,);
  background-color: white;
  background-repeat: no-repeat;
  background-position: 12px 11px;
}

.logout-btn {
  padding: 12px 16px 12px 16px;
}

@media screen and (max-width: 500px) {
  .header {
    padding: 0px 15px 0px 5px;
  }

  .header h1 {
    font-size: 1.5rem;
  }

  .account-menu {
    right: 5px;
    top: 96px;
  }

  .account-menu::after {
    right: 12px;
  }
}
